/* TABS SWITHER */
function createTabs(id) {
  // vars
  const tabId = document.getElementById(`${id}-tabs`);
  const tabNavs = tabId.querySelectorAll(`.${id}__nav`);
  const tabContents = tabId.querySelectorAll(`.${id}__tab-content`);
  // logic
  [...tabNavs].forEach((nav, index) => {
    nav.addEventListener("click", () => {
      // reset
      [...tabNavs].forEach(nav => nav.classList.remove(`${id}__nav--active`));
      [...tabContents].forEach(content =>
        content.classList.remove(`${id}__tab-content--active`)
      );
      // set
      nav.classList.add(`${id}__nav--active`);
      tabContents[index].classList.add(`${id}__tab-content--active`);
    });
  });
  // default
  tabNavs[0].click();
}

function createAccordition(id, callback) {
  const blocks = document
    .getElementById(id)
    .querySelectorAll(".accordition__block");
  [...blocks].forEach(block => {
    block.firstElementChild.addEventListener("click", elem => {
      block.classList.toggle("accordition__block--active");
      const panel = elem.target.nextElementSibling;
      if (panel.style.height && panel.style.height !== "0px") {
        panel.style.height = "0";
      } else {
        panel.style.height = panel.scrollHeight + 30 + "px";
      }
    });
  });
}

document.querySelectorAll(".plan__nav").forEach((item, index) => {
  item.addEventListener("click", () => {
    let tab = document
      .querySelectorAll(".plan__tab-content")[index].querySelector(".accordition__title");
    if (!tab.parentElement.classList.contains("accordition__block--active")) {
      tab.click();
    }
  });
  if (index === 0) item.click();
});

