// FORM
$(".btn-call-form").click(function () {
  $(".a1-form__layout").addClass("a1-form__layout--active");
  $('body').css('overflowY', 'hidden');
});

$(".a1-form__close").click(function () {
  $(".a1-form__layout").removeClass("a1-form__layout--active");
  $('body').css('overflowY', 'auto'); 
});

$('.send-form').submit(function () {
  const name = $('form input[name=firstName]').val() + ' ' + $('form input[name=lastName]').val(),
    phone = $('form input[name=phone]').val(),
    email = $('form input[name=email]').val(),
    country = $('form input[name=country]').val();

  $('.a1-form__error, .a1-form__success').hide();
  $.post('/api/v1/claim', {
      name: name,
      email: email,
      phone: phone,
      program_id: '2aad83be-77a7-4aed-942c-57158339db90',
      country: country
  }, function (response) {
      $('.a1-form__success').addClass('a1-form__success--visible');
  }, 'json').fail(function (response) {
      var responseJson = JSON.parse(response.responseText);
      var message = responseJson.data.message;
      $('.a1-form__error').addClass('a1-form__error--visible');
      $('.a1-form__error').text(message);
  });

  return false;
});